import React from 'react';
import PropTypes from 'prop-types';
import {Redirect} from 'react-router-dom';
import AppTemplate from '../appTemplate.jsx';
import SearchItems from './searchItems.jsx';
import PodcastItems from './podcastItems.jsx';
import {PodHeader,PodBody} from '../../styledComponents/styledComponents.jsx';


export default class PodcastsDashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      redirectTo : null
    }

  }

  componentWillMount() {
    const {token} = this.context.store.getState();
    //alert(token.value);
    if(!token.value)
      this.setState({redirectTo : '/signIn'});

  }
  render() {
    const {redirectTo} = this.state;
    const {user} = this.context.store.getState();

    return (
      (redirectTo)?
      <Redirect to={redirectTo} />:
      <AppTemplate>
            <PodHeader>
               <p><span>Hello</span> <br/>{user.username}</p>
               <SearchItems />
            </PodHeader>
            <PodBody>
               <PodcastItems  />
            </PodBody>
      </AppTemplate>);
  }
}

PodcastsDashboard.propTypes = {
};

PodcastsDashboard.contextTypes = {
  store : PropTypes.object
}



